.userContainer{
    background-color: rgb(255, 189, 56);
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
}

.userContainer h1{
    background-color: whitesmoke;
    padding: 5px;
    border: solid rgb(255, 255, 255) 2px;
    box-shadow: black 0.2rem 0.2rem 0.2rem 0.2rem;
}

.userContainer__box{
    background-color: white;
    padding: 10px;
    border: solid black 2px;
    box-shadow: black 0.2rem 0.2rem 0.2rem 0.2rem;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.userContainer__box--insideTitle{
    font-weight: bold;
}

.userContainer__box--title{
    display: flex;
    flex-direction: column;
    margin-left: 10%;
}

.userContainer__box--credit :nth-child(2){
    font-weight: bold;
    background-color: rgb(216, 25, 25);
    color: white;
}

.userContainer__box--credit :last-child{
    font-weight: bold;
    color: black;
    text-decoration: none;
    border: solid rgb(0, 0, 0) 1px;
    padding: 5px;
    border-radius: 5px;
    font-size: 1.5rem;
}

.userContainer__box--credit :last-child:hover{
    font-weight: bold;
    color: rgb(255, 255, 255);
    text-decoration: none;
    border: solid rgb(0, 0, 0) 1px;
    background-color: rgb(116, 116, 116);
}

.fa-check-circle{
    align-self: center;
    width: fit-content;
    color: green;
}