.homeContainer{
    background-color: #FAFAFA;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 1rem;
    color: white;
    margin: 0;
    overflow: auto;
    color: rgb(30, 27, 27);
}

.homeContainer__title{
    text-decoration: underline;
    margin-top: 10vh;
}

.homeContainer__hi{
    padding: 5% 3% ;
    text-align: justify;
    font-size: 1.2rem;
}

.homeContainer__email{
    color: black;
    font-size: 1.5rem;
    text-align: center;
}

.homeContainer__contact{
    display: flex;
    justify-content: space-around;
    width: 20vw;
    margin-bottom: 10vh;
}

.fa-facebook-square, .fa-github-square, .fa-linkedin{
    font-size: 3rem;
    color: black;
}

@media (max-width: 500px){
    .homeContainer__title{
        align-self: center;
        margin-left: 15%;       
        margin-top: 2vh;
  
    }
    .homeContainer{
        width: 100%;
        height: 100%;
        padding-top: 5%;
    }
}    

@media (max-width: 600px){
    .homeContainer__contact{
        
        width: 60vw;
    }
}

