.loginscreen{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center; 
    color: rgb(57, 57, 57);
}

.loginscreen__main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 10;
    text-align: center;
}

.loginscreen__main__firstTitle{
    font-weight: bold;
    font-size: 3.5rem;
    margin-bottom: 10%;
}

.loginscreen__main--box{ 
    padding: 5%;
    display: flex;
    flex-direction: column;     
    font-size: 1.5rem;
    border-radius: 3%;
    letter-spacing: 0.5rem;
}

.login__screen--buttom{
    background-color: rgb(128, 223, 237); 
    box-shadow: 5px 5px 2px 0 rgba(0,0,0,0.20);
    margin: 2%;
    padding: 2% 0;
    border-style: none;
    border-radius: 8px;
    font-weight: bold;
    font-size: 1.5rem;
    width: 25vw;
}

.login__screen--buttom:hover {
    background-color: whitesmoke;
}

#loginscreen__tsparticles{
    position: absolute;
    opacity: 0.6;
    width: 100vw;
    height: 100vh;
}

@media (max-width: 600px){

    .loginscreen__main__firstTitle{
        
        font-size: 2rem;
    }

}
